import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'slick-slider/slick/slick';
import Parallax from 'parallax-js';
import '@fancyapps/fancybox';
import Cocoen from 'cocoen';
import Inputmask from 'inputmask';
import 'jquery-validation';
import 'chosen-js';

$(() => {
	if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
		const $viber = $('.js-vib');
		const $viberMap = $('.js-vib-map');
        $viber.attr('href', 'viber://add?number=79037900722');
		$viberMap.attr('href', 'viber://add?number=79852272023');
		// $('difference__slide_mobile').css({display: 'none'});
	}
	svg4everybody();
	const menuButton = document.querySelector('.burger-menu');
	menuButton.addEventListener('click', function () {
		const lines = menuButton.children;
		for (const item of lines) {
			item.classList.toggle('burger-menu__line_active');
		}
		const menu = document.querySelector('.header__mobile-menu');
		menu.classList.toggle('header__mobile-menu_active');
		if ($('.burger-menu').hasClass('pressed')) {
			menuButton.classList.toggle('pressed');
			// overlayDisable();
		}else {
			overlayActivate();
			menuButton.classList.toggle('pressed');
		}
	});
	const $select = $('.chosen-select');
	$select.chosen({
		// eslint-disable-next-line
		placeholder_text_multiple: "Выберите услуги...",
		width: '100%'
	});
	const im = new Inputmask('+7 ( 999 ) 999-99-99');
	const tels = document.querySelectorAll('input[type=tel]');
	im.mask(tels);
	const date = new Inputmask('99 /99 /9999');
	const dateField = document.querySelector('input[name=date]');
	date.mask(dateField);
	const time = new Inputmask('99:99');
	const timeField = document.querySelector('input[name=time]');
	time.mask(timeField);

	const firstScreen = document.getElementById('main-clouds');
	// eslint-disable-next-line
	const mainClouds = new Parallax(firstScreen);
	const salonScreen = document.getElementById('salon-clouds');
	// eslint-disable-next-line
	const salonClouds = new Parallax(salonScreen);
	const consultScreen = document.getElementById('consult-clouds');
	// eslint-disable-next-line
	const consultClouds = new Parallax(consultScreen);
	$('.js-navlink').on('click', function () {
		menuButton.click();
		overlayDisable();
	});
	$('.js-master-item').on('click', function () {
		const id = $(this).data('master');
		const content = $('.js-master-tab[data-master = "' + id + '"]');
		const slider = $('.master-slider[data-master = "' + id + '"]');
		// $('.js-master-tab').css({display: 'none'});
		$('.js-master-tab').removeClass('active');
		// content.css({display: 'block'});
		content.addClass('active');
		$('.js-master-item').removeClass('active-item');
		$(this).addClass('active-item');
		$('.master-slider').removeClass('active-slider');
		slider.addClass('active-slider').find('.js-master-slider').slick({
			slidesToShow: 4,
			slidesToScroll: 1,
			dots: true,
			arrows: true,
			prevArrow: '.master-slider__left-btn',
			nextArrow: '.master-slider__right-btn',
			lazyLoad: 'ondemand',
			dotsClass: 'my-dots',
			responsive: [
				{
					breakpoint: 1450,
					settings: {
						slidesToShow: 3,
						slidesToScroll: 1
					}
				},
				{
					breakpoint: 930,
					settings: {
						slidesToShow: 3,
						slidesToScroll: 1
					}
				},
				{
					breakpoint: 710,
					settings: {
						slidesToShow: 2,
						slidesToScroll: 1
					}
				},
				{
					breakpoint: 540,
					settings: {
						slidesToShow: 1,
						slidesToScroll: 1
					}
				}
			]
		});
	});
	$('.master-slider.active-slider').find('.js-master-slider').slick({
		slidesToShow: 4,
		slidesToScroll: 1,
		arrows: true,
		prevArrow: '.master-slider__left-btn',
		nextArrow: '.master-slider__right-btn',
		dots: true,
		lazyLoad: 'ondemand',
		dotsClass: 'my-dots',
		responsive: [
			{
				breakpoint: 1450,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 930,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 710,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 540,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1
				}
			}
		]
	});
	$('[data-fancybox="serts"]').fancybox({
		loop: false,
		toolbar: false,
		smallBtn: true,
		thumbs: true,
		helpers: {
			thumbs: {
				width: 50,
				height: 50
			}
		}
	});
	document.querySelectorAll('.cocoen').forEach(function (element) {
		new Cocoen(element);
	});
	// new Cocoen(document.querySelector('.cocoen'));
	$('.js-difference-slider').slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		centerMode: true,
		centerPadding: 20,
		prevArrow: '.difference__left-btn',
		nextArrow: '.difference__right-btn',
		draggable: false,
		swipe: false
	});
	$('.js-salon-slider').slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		centerMode: true,
		arrows: false,
		centerPadding: 0,
		dots: true,
		dotsClass: 'my-dots-salon'
	});

	$('.js-service-list').each(function (index) {
		$(this).children('.js-service-item').first().addClass('active-item');
		$(this).children('.js-service-item').first().find('.js-item-price').addClass('active-item');
		$(this).parentsUntil('.service').find('.js-curr-pic').first().addClass('active-pic');
	});

	$('.js-service-item').on('click', function () {
		const id = $(this).data('type');
		const slider = $('.js-curr-pic[data-type = "' + id + '"]');

		$(this).parents('.js-service-list').children('.js-service-item').removeClass('active-item');
		$(this).parents('.js-service-list').children('.js-service-item').find('.js-item-price').removeClass('active-item');
		// $(this).parents('.')
		$(this).addClass('active-item');
		$(this).find('.js-item-price').addClass('active-item');
		$(this).parentsUntil('.service').find('.js-curr-pic').removeClass('active-pic');
		slider.addClass('active-pic');
	});
		/** Video */
	const play = document.querySelector('#play');
	const contain = document.querySelectorAll('.video-box');
	const videoPlayer = document.querySelectorAll('.video-section__video-wrap');
	const video = document.querySelectorAll('.video-section__frame');
	play.addEventListener('click', () => {
		contain[0].classList.add('video-box_hidden');
		videoPlayer[0].classList.add('video-section__video-wrap_visible');
		video[0].contentWindow.postMessage(JSON.stringify({event: 'command', func: 'playVideo'}), '*');
	});

	$('.js-slider-feedback').slick({
		slidesToShow: 3,
		slidesToScroll: 1,
		centerMode: true,
		centerPadding: 20,
		prevArrow: '.feedback__left-btn',
		nextArrow: '.feedback__right-btn',
		responsive: [
			{
				breakpoint: 930,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 710,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1
				}
			},
			{
				breakpoint: 540,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1
				}
			}
		]
	});
	const $overlay = $('.overlay');
	const $popups = $('.js-popup');
	const $popupConsultation = $('.js-popup-consultation');
	const $popupDisclaimer = $('.js-popup-disclaimer');
	// const $disclaimerBtn = $('.js-disclaimer');
	const $consultationBtn = $('.js-consultation-btn');
	const $popupCallback = $('.js-popup-callback');
	const $callbackBtn = $('.js-callback-btn');
	const $popupThanks = $('.js-popup-thanks');
	const $popupThanksOrder = $('.js-popup-thanks-order');
	const $close = $('.js-close');
	const $popupSign = $('.js-popup-sign');
	const $popupSignBtn = $('.js-popup-sign-btn');
	const $popupPrice = $('.js-popup-price');
	const $popupPriceBtn = $('.js-popup-price-btn');

	function setPopupPosition() {
		const popupConsultationMargin = ($(window).innerHeight() - $popupConsultation.innerHeight()) / 2;
		$popupConsultation.css('marginTop', popupConsultationMargin < 30 ? '30px' : popupConsultationMargin + 'px');
		const popupCallbackMargin = ($(window).innerHeight() - $popupCallback.innerHeight()) / 2;
		$popupCallback.css('marginTop', popupCallbackMargin < 30 ? '30px' : popupCallbackMargin + 'px');
		const popupThanksMargin = ($(window).innerHeight() - $popupThanks.innerHeight()) / 2;
		$popupThanks.css('marginTop', popupThanksMargin < 30 ? '30px' : popupThanksMargin + 'px');
		const popupThanksMarginOrder = ($(window).innerHeight() - $popupThanksOrder.innerHeight()) / 2;
		$popupThanksOrder.css('marginTop', popupThanksMarginOrder < 30 ? '30px' : popupThanksMarginOrder + 'px');
		const popupDisclaimerMargin = ($(window).innerHeight() - $popupDisclaimer.innerHeight()) / 2;
		$popupDisclaimer.css('marginTop', popupDisclaimerMargin < 30 ? '30px' : popupDisclaimerMargin + 'px');
		const popupSignMargin = ($(window).innerHeight() - $popupSign.innerHeight()) / 2;
		$popupSign.css('marginTop', popupSignMargin < 30 ? '30px' : popupSignMargin + 'px');
		const popupPriceMargin = ($(window).innerHeight() - $popupPrice.innerHeight()) / 2;
		$popupPrice.css('marginTop', popupPriceMargin < 30 ? '30px' : popupPriceMargin + 'px');

	}
	function overlayActivate() {
		$overlay.addClass('active-overlay');
		document.body.style.overflowY = 'hidden';
		setPopupPosition();
	}
	function overlayDisable() {
		$overlay.removeClass('active-overlay');
		document.body.style.overflowY = 'auto';
	}
	$callbackBtn.on('click', function () {
		if (($('.burger-menu')) && $('.burger-menu').hasClass('pressed')) {
			menuButton.click();
			menuButton.classList.toggle('pressed');
		}

		$popupCallback.addClass('active-overlay');
		overlayActivate();
	});
	$consultationBtn.on('click', function () {
		$popupConsultation.addClass('active-overlay');
		overlayActivate();
	});
	$popupSignBtn.on('click', function () {
		$popupSign.addClass('active-overlay');
		overlayActivate();
	});
	$popupPriceBtn.on('click', function () {
		$popupPrice.addClass('active-overlay');
		overlayActivate();
	});

	$('a[href^="#"]').on('click', function (event) {
		event.preventDefault();
		const sc = $(this).attr('href');
		let dn = $(sc).offset().top;
		$('html, body').animate({scrollTop: dn}, 1000);
	});
	$(window).resize(setPopupPosition);
	$close.on('click', function () {
		const ifDisc = $(this).parents('.js-popup').hasClass('js-popup-disclaimer');
		const ifCall = $(this).parents('.js-popup').hasClass('js-popup-callback');
		const ifLeave = $(this).parents('.js-popup').hasClass('js-popup-add');
		if (ifDisc) {
			$popupDisclaimer.removeClass('active-overlay');
			const openPopups = $popups.hasClass('active-overlay');
			if (!openPopups) {overlayDisable();}
		}else if (ifCall) {
			$popupCallback.removeClass('active-overlay');
			overlayDisable();
		}else if (ifLeave) {
			// $popupAdd.remove();
			overlayDisable();
			// $popupAdd.addClass('shown');
		}
		else {
			$popups.removeClass('active-overlay');
			overlayDisable();
		}
	});

	$overlay.on('click', function (e) {
		if (e.target !== $popups) {
			// if ($popupAdd.hasClass('active')) {
			// 	$popupAdd.remove();
			// 	// $popupAdd.addClass('shown');
			// };
			$popups.removeClass('active-overlay');
			if (($('.burger-menu')) && $('.burger-menu').hasClass('pressed')) {
				menuButton.click();
				overlayDisable();
			}
			overlayDisable();

		}
	});
	function showThanks() {
		$popups.removeClass('active-overlay');
		$popupThanks.addClass('active-overlay');
		setPopupPosition();
	}
	function showOrder() {
		$popups.removeClass('active-overlay');
		$popupThanksOrder.addClass('active-overlay');
		setPopupPosition();
	}
	function formSend(form) {
		if (form.valid()) {
			let msg = form.serialize();
			console.log(msg);
			$.ajax({
				type: 'POST',
				url: './include/mail.php',
				data: msg,
				success: () => {
					if (form.get(0).type.value === 'sign') {
						showOrder();
					}
					else {
						showThanks();
					}
				},
				error: xhr => {
					// eslint-disable-next-line
					alert('Возникла ошибка: ' + xhr.responseCode);
				}
			});
		}
	}

	$('[type=submit]').on('click', function (e) {
		e.preventDefault();
		formSend($(this).parents('form'));
	});
	const rules = {
		rules: {
			tel: {
				normalizer: value => {
					return value.replace(/[- _\)\(]/g, '');
				},
				required: true,
				minlength: 12
			}
		},
		messages: {
			tel: {
				required: 'Это обязательное поле',
				minlength: 'Некорректный номер телефона'
			}
		}
	};
	$('.popup-callback__form').validate(rules);
	$('.popup-consult__form').validate(rules);
	$('.popup-sign__form').validate(rules);
	$('.consult__form').validate(rules);
});



